.textarea {
  font-weight: normal;
  font-family: "FS Albert", Arial, "Helvetica Neue", Helvetica, sans-serif;
  font-weight: 400;
  background-color: #fff;
  border: 0.125rem solid #e5e5e5;
  border-radius: 0.375rem;
  display: block;
  font-size: 1.125rem;
  line-height: 1.5rem;
  min-height: 9rem;
  padding: 0.8125rem 2.8125rem 0.8125rem 0.9375rem;
  width: 100%;
}

.textarea::-ms-clear {
  display: none;
}

.textarea::-webkit-search-decoration, .textarea::-webkit-search-cancel-button, .textarea::-webkit-search-results-button, .textarea::-webkit-search-results-decoration {
  -webkit-appearance: none;
}

.textarea::placeholder {
  color: #767676;
}

.textarea:hover::placeholder, .textarea.is-hovered::placeholder {
  color: #333f48;
}

.textarea:focus, .textarea.is-focused {
  border-color: #009ede;
  box-shadow: 0 0 0 0.125rem #009ede !important;
}

.textarea:focus::placeholder, .textarea.is-focused::placeholder {
  color: #333f48;
}

.textarea.error {
  border-color: #dd3817;
}

.textarea.success {
  background-image: url("/images/components/form-elements/success.svg");
  background-position: right 0.875rem top 0.875rem;
  background-repeat: no-repeat;
  background-size: 1.5rem;
  border-color: #bad80a;
}

.textarea.disabled, .textarea.disabled:hover, .textarea.disabled:focus {
  background-color: #f8f8f8;
  border-color: #e5e5e5;
  color: #767676;
  cursor: not-allowed;
}

.textarea.disabled::placeholder, .textarea.disabled:hover::placeholder, .textarea.disabled:focus::placeholder {
  color: #767676;
}
